import React from 'react';
import styles from './styles.module.css';
import { productPropType } from '../../constants/propTypes';
import NutritionValue from './components/nutrition-value';

/*    ,       */
function IngredientDetails({ ingredient }) {
  return (
    <div className={`${styles.container} mb-15`}>
      <img className="mb-4" src={ingredient.image_large} alt={ingredient.name} />
      <p className="text text_type_main-medium mb-8">
        {ingredient.name}
      </p>
      <ul className={`${styles.nutritionList} `}>
        <NutritionValue text="," value={ingredient.calories} />
        <NutritionValue text=", " value={ingredient.proteins} />
        <NutritionValue text=", " value={ingredient.fat} />
        <NutritionValue text=", " value={ingredient.carbohydrates} />
      </ul>
    </div>
  );
}

IngredientDetails.propTypes = {
  ingredient: productPropType.isRequired,
};

export default IngredientDetails;
